// scroll-header
window.addEventListener('scroll', e => {
	let header = document.getElementById('header').classList;
	let active_class = "header-scrolled";

	if(pageYOffset > 0) header.add(active_class)
	else header.remove(active_class)
});

// video dealer
function videoPlay() {
	const video = document.querySelector('.dealer-top__video video');
	const play = document.querySelector('.dealer-top__circle');
	const text = document.querySelector('.dealer-top__text');
	const gradient = document.querySelector('.dealer-top__video')
	play.addEventListener('click', () =>{
		video.setAttribute('controls', 'controls');
		text.style.display = 'none';
		gradient.classList.add('dealer-top__video-active');
		video.play();
	});
	video.addEventListener('click', () =>{
		video.removeAttribute('controls');
		text.style.display = 'flex';
		gradient.classList.remove('dealer-top__video-active');
		video.pause();
		play.classList.add('dealer-top__circle-active');
	})
}
videoPlay()

// about
function aboutClass(){
	const arrow = document.querySelector('.about-preview__top-arrow img');
	arrow.addEventListener('click', () =>{
		const about = document.querySelector('.about');
		about.classList.toggle('about-active');
	});

}
aboutClass();

// reviewsPopap
function reviewsPopap(){
	const reviews = document.querySelectorAll('.reviews__item');
	const close = document.querySelector('.reviews__close img');
	
		for (let i = 0; i < reviews.length; i++) {
			reviews[i].addEventListener('click', () => {
				reviews[i].classList.add('reviews-fixed');
				close.style.display = 'block';
			});
			close.addEventListener('click', () =>{
				reviews[i].classList.remove('reviews-fixed');
				close.style.display = 'none';
			})
		};
		

}
reviewsPopap();










