$leftLinks.forEach(el => {
	el.addEventListener('mouseenter', (e) => {
		let self = e.currentTarget;
		let selfClass = self.getAttribute('href');
		let currentElement = document.querySelector(`.office-bottom__map a[href="${selfClass}"]`);
		let currentCircle = currentElement.querySelectorAll('circle');
		let currentPath = currentElement.querySelectorAll('path');
		if (currentCircle) currentCircle.forEach(el => el.style.cssText = `fill:#fff; stroke:#fff;`);
		if (currentPath) currentPath.forEach(el => el.style.cssText = `fill: #FFB577; stroke:#F2F2F2; stroke-width:2;`);
		self.classList.add('office-active');

	});
	el.addEventListener('mouseleave', (e) => {
		let self = e.currentTarget;
		let selfClass = self.getAttribute('href');
		let currentElement = document.querySelector(`.office-bottom__map a[href="${selfClass}"]`);
		let currentPath = currentElement.querySelectorAll('path');
		let currentCircle = currentElement.querySelectorAll('circle');
		if (currentCircle) currentCircle.forEach(el => el.style.cssText = ``);
		if (currentPath) currentPath.forEach(el => el.style.cssText = ``);
		self.classList.remove('office-active');

	});

});
$mapLinks.forEach(el => {
	el.addEventListener('mouseenter', (e) => {
		let self = e.currentTarget;
		let selfClass = self.getAttribute('href');
		let currentElement = document.querySelector(`.office-top a[href="${selfClass}"]`);
		let currentCircle = self.querySelectorAll('circle');
		let currentPath = self.querySelectorAll('path');
		if (currentCircle) currentCircle.forEach(el => el.style.cssText = `fill:#fff; stroke:#fff;`);
		if (currentPath) currentPath.forEach(el => el.style.cssText = `fill: #FFB577; stroke:#F2F2F2; stroke-width:2;`);
		currentElement.classList.add('office-active');
	});
	el.addEventListener('mouseleave', (e) => {
		let self = e.currentTarget;
		let selfClass = self.getAttribute('href');
		let currentElement = document.querySelector(`.office-top a[href="${selfClass}"]`);
		let currentCircle = self.querySelectorAll('circle');
		let currentPath = self.querySelectorAll('path');
		if (currentCircle) currentCircle.forEach(el => el.style.cssText = ``);
		if (currentPath) currentPath.forEach(el => el.style.cssText = ``);
		currentElement.classList.remove('office-active');
	});
	el.addEventListener('click', (e) => {
		e.preventDefault();
		let self = e.currentTarget;
		let selfClass = self.getAttribute('href');
		let currentElement = document.querySelector(`.office-top a[href="${selfClass}"]`);
		let id = parseInt(currentElement.dataset.id);
		requestData(id);
	});
});

