for (let i = 0; i < $leftLinks.length; i++) {
	$leftLinks[i].addEventListener('click', function (){
	
		if ($mapLinks[i].classList.contains("map-links-active")) {
			$mapLinks[i].classList.remove("map-links-active");
		} else {
			$mapLinks[i].classList.add("map-links-active");
		}
		if ($info.classList.contains("office-info-active")) {
			$info.classList.remove("office-info-active");
		} else {
			$info.classList.add("office-info-active");
		}
	});
}

$leftLinks.forEach(menuClick);
function menuClick(itm) {
    itm.addEventListener("click", function() {
        let currentBtn = itm;
        if( ! currentBtn.classList.contains('menu-top-links-color') ) {
            $leftLinks.forEach(function(itm) {
                itm.classList.remove('menu-top-links-color');
            });
            currentBtn.classList.add('menu-top-links-color');
        };
    });

};


