// tabs map
const tabsBtn   = document.querySelectorAll(".office-info-top__btn");
const tabsItems = document.querySelectorAll(".office-info-bottom");


tabsBtn.forEach(onTabClick);
function onTabClick(item) {
    item.addEventListener("click", function() {
        let currentBtn = item;
        let tabId = currentBtn.getAttribute("data-tab");
        let currentTab = document.querySelector(tabId);


        if( ! currentBtn.classList.contains('tabs-active') ) {
            tabsBtn.forEach(function(item) {
                item.classList.remove('tabs-active');
            });
    
            tabsItems.forEach(function(item) {
                item.classList.remove('tabs-active');
            });
    
            currentBtn.classList.add('tabs-active');
            currentTab.classList.add('tabs-active');
        };

    });
	 document.querySelector('.office-info-top__btn').click();
}